/*

  Cabana GitHub DBC List
  ~~~~~~~~~~~~~~~~~~~~~~

*/
.cabana-meta-header:after, .signals-legend-entry-form-field:after {
  content: "";
  display: table;
  clear: both;
}

.cabana-dbc-list-header a {
  color: #484848;
  font-weight: 600;
  text-decoration: none;
}
.cabana-dbc-list-header a i {
  padding-right: 5px;
}
.cabana-dbc-list-header .form-field {
  margin: 11px 0;
}
.cabana-dbc-list-files {
  background: rgba(0, 0, 0, 0.02);
  border: #a2a2a2;
  border-radius: 5px;
}
.cabana-dbc-list-file {
  cursor: pointer;
  font-size: 14px;
  padding: 6px;
  padding-left: 30px;
  position: relative;
  transition-duration: 0.1s;
}
.cabana-dbc-list-file:not(:last-child) {
  border-bottom: 1px solid #e1e1e1;
}
.cabana-dbc-list-file:hover {
  background: rgba(0, 0, 0, 0.025);
}
.cabana-dbc-list-file:before {
  content: "";
  border: 1px solid #d4d4d4;
  border-radius: 12px;
  display: block;
  height: 12px;
  left: 8px;
  top: 7px;
  position: absolute;
  transition-duration: 0.2s;
  width: 12px;
}
.cabana-dbc-list-file:after {
  background: transparent;
  border-radius: 8px;
  content: "";
  display: block;
  height: 8px;
  left: 11px;
  top: 10px;
  position: absolute;
  transition-duration: 0.2s;
  width: 8px;
}
.cabana-dbc-list-file.is-selected {
  background: rgba(0, 0, 0, 0.04);
  font-weight: 600;
}
.cabana-dbc-list-file.is-selected:before {
  border-color: #258fda;
}
.cabana-dbc-list-file.is-selected:after {
  background-color: #258fda;
}
